$Montserrat: ('-Thin', 100),
('-Light', 300),
('-Regular', 400),
('-Medium', 500),
('-SemiBold', 600),
('-Bold', 700),
('-ExtraBold', 800),
('-Black', 900);

@each $name, $weight in $Montserrat {
    @font-face {
        font-family: 'Montserrat';
        src: url(../../fonts/Montserrat/Montserrat#{$name}.ttf);
        font-weight: $weight;
    }
} 